                    {
                        name: "孝感",
                        value: 35
                    },
                    {
                        name: "黄冈",
                        value: 2662
                    },
                    {
                        name: "咸宁",
                        value: 534
                    },
                    {
                        name: "随州",
                        value: 1160
                    },
                    {
                        name: "恩施",
                        value: 229
                    },
                    {
                        name: "仙桃",
                        value: 480
                    },
                    {
                        name: "天门",
                        value: 362
                    },
                    {
                        name: "潜江",
                        value: 94
                    },
                    {
                        name: "神农架",
                        value: 10
                    }
                ],
                convertedData: [],
                option: {},
                chart: null,
            }
        },
        methods: {
            convertedDataFn() {
                this.convertedData = [
                    this.convertData(this.data),
                    this.convertData(this.data.sort(function (a, b) {
                        return b.value - a.value;
                    }).slice(0, 6))
                ]
            },
            convertData(data) {
                var res = [];
                for (var i = 0; i < data.length; i++) {
                    var geoCoord = this.geoCoordMap[data[i].name];
                    if (geoCoord) {
                        res.push({
                            name: data[i].name,
                            value: geoCoord.concat(data[i].value)
                        });
                    }
                }
                return res;
            },
            drawEchart() {
                this.option = {
                    baseOption: {
                        backgroundColor: {
                            type: 'linear',
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [{
                                    offset: 0,
                                    color: '#1e1e1e'
                                },
                                {
                                    offset: 0.5,
                                    color: '#111111'
                                },
                                {
                                    offset: 1,
                                    color: '#000000'
                                }
                            ],
                        },
                        animationDurationUpdate: 1000,
                        title: {
                            padding: 20,
                            text: '虚构疫情状况',
                            subtext: '国家卫健委测',
                            sublink: 'http://www.nhc.gov.cn',
                            x: 'left',
                            textStyle: {
                                color: '#fff'
                            },
                            subtextStyle: {
                                fontStyle: 'italic'
                            }
                        },
                        legend: {
                            icon: 'circle',
                            orient: 'vertical',
                            top: '7%',
                            left: 'right',
                            data: [{
                                    name: 'Top5',
                                },
                                {
                                    name: '散点',
                                },
                                {
                                    name: '热图',
                                }
                            ],
                            textStyle: {
                                color: '#fff'
                            }
                        },
                        toolbox: {
                            iconStyle: {
                                normal: {
                                    borderColor: '#fff'
                                },
                                emphasis: {
                                    borderColor: '#abc'
                                }
                            },
                            feature: {
                                restore: {},
                                saveAsImage: {}
                            }
                        },
                        tooltip: {
                            trigger: 'item'
                        },
                        grid: {
                            right: '2%',
                            top: '20%',
                            bottom: '15%',
                            width: '15%'
                        },
                        xAxis: {
                            type: 'value',
                            scale: true,
                            position: 'top',
                            boundaryGap: false,
                            splitNumber: 4,
                            splitLine: {
                                show: false
                            },
                            axisLine: {
                                show: false
                            },
                            axisTick: {
                                show: false
                            },
                            axisLabel: {
                                margin: 2,
                                textStyle: {
                                    color: '#aaa'
                                }
                            },
                        },
                        yAxis: {
                            type: 'category',
                            nameGap: 95,
                            axisLine: {
                                show: false
                            },
                            axisTick: {
                                show: false
                            },
                            axisLabel: {
                                interval: 0,
                                textStyle: {
                                    color: '#fff'
                                }
                            },
                            data: []
                        },
                        visualMap: {
                            min: 0,
                            max: 40000,
                            pieces: [{
                                    min: 2000
                                },
                                {
                                    min: 1500,
                                    max: 2000
                                },
                                {
                                    min: 1000,
                                    max: 1500
                                },
                                {
                                    min: 500,
                                    max: 1000
                                },
                                {
                                    min: 300,
                                    max: 500
                                },
                                {
                                    min: 200,
                                    max: 300
                                },
                                {
                                    min: 5,
                                    max: 200
                                },
                                {
                                    max: 5
                                }
                            ],
                            inRange: {
                                color: ['#50a3ba', '#eac736', '#d94e5d']
                            },
                            textStyle: {
                                color: '#fff'
                            }
                        },
                        geo: {
                            roam: true, //缩放平移

                            scaleLimit: {
                                min: 0.5,
                                max: 10,
                            },
                            map: 'china',
                            itemStyle: {
                                areaColor: '#333333',
                                borderColor: '#000',
                            },
                            emphasis: {
                                itemStyle: {
                                    areaColor: '#123555',
                                    shadowColor: '#000',
                                    shadowBlur: 10
                                },
                                label: {
                                    show: false
                                }
                            },
                            regions: [{
                                name: '湖北',
                                itemStyle: {
                                    areaColor: '#222222'
                                },
                                emphasis: {
                                    itemStyle: {
                                        areaColor: '#111111',
                                        shadowColor: '#000',
                                        shadowBlur: 10
                                    },
                                }
                            }],
                        },
                        series: [{
                                name: '热图',
                                type: 'heatmap',
                                coordinateSystem: 'geo',
                                data: this.convertedData[0]
                            },
                            {
                                name: '散点',
                                type: 'scatter',
                                coordinateSystem: 'geo',
                                data: this.convertedData[0],
                                symbolSize: function (val) {
                                    return (5 + Math.log2(val[2]));
                                },
                                label: {
                                    normal: {
                                        formatter: '{b}',
                                        position: 'right',
                                        show: false
                                    },
                                    emphasis: {
                                        show: true
                                    }
                                },
                                itemStyle: {
                                    normal: {
                                        color: '#ddb926'
                                    }
                                },
                                tooltip: {
                                    trigger: 'item',
                                    formatter: function (params) {
                                        return params.name + ' : ' + params.value[2];
                                    }
                                },
                            },
                            {
                                id: 'bar',
                                zlevel: 2,
                                type: 'bar',
                                symbol: 'none',
                                itemStyle: {
                                    color: '#ddd',
                                },
                                emphasis: {
                                    color: '#fff',
                                    shadowColor: 'rgba(255, 0, 0, 0.5)',
                                    shadowBlur: 10
                                },
                                data: []
                            }
                        ]
                    },
                    options: []
                }
                this.chart = this.$echarts.init(this.$refs.chart);
                this.chart.showLoading({
                    animation: 'QuarticIn',
                    text: 'Loading',
                });
                this.chart.hideLoading();
                this.chart.setOption(this.option);
            }
        },
        created() {
            this.convertedDataFn();
        },
        mounted() {
            this.drawEchart()
        }
    }
</script>
<style lang="scss" scoped>
    .abc {
        width: 100%;
        height: 100%;

        >div {
            width: 100%;
            height: 100%;
        }
    }
</style>